/* Home css */
.home {
    overflow-x:hidden;
}
/* //Home css */

/* Header css */
.header {
    box-shadow: 0 0 10px 0px gray;
}

.headerWrap {
    position:relative;
    height:200px;
    width:800px;
    margin:0 auto;
    display:flex;
    flex-direction:column;
}

.headerTopWrap {
    display:flex;
    justify-content:center;
    height:160px;
}

.mainLogo {
    width:200px;
    height:150px;
}

.headerIconsWrap {
    position:absolute;
    right:0;
    top:60px;
    display:flex;
    width:90px;
    justify-content:space-between;
}

.headerIconsWrap li:hover {
    cursor:pointer;
}

.headerIcons {
    width:40px;
    height:40px;
    color:black;
}

.headerBtnWrap {
    width:790px;
    height:100%;
    margin:0 auto;
    display:flex;
    justify-content:space-between;
    position:relative;
}

.headerBtnWrap li {
    height:100%;
}

.headerMenus {
    display:block;
    color:burlywood;
    transition:0.2s;
    height:100%;
}

.headerMenus:hover {
    color:antiquewhite;
}

.headerTypeSubMenuWrap {
    position:absolute;
    top:35px;
    left:0;
    width:100%;
    height:380px;
    background:white;
    box-shadow:0 0 3px 0.1px gray;
    transition:0.2s;
    opacity:0;
    visibility:hidden;
}
 
.headerTypeSubMenuWrap div {
    width:95%;
    margin:40px 0 0 40px;
}
 
.headerTypeSubMenuWrap h4 {
    font-size:17px;
    line-height:17px;
    width:fit-content;
    margin-bottom:20px;
}
 
.FaAngleRight {
    vertical-align:middle;
}

.headerTypeSubMenuWrap ul {
    display:inline-flex;
    flex-direction:column;
    width:140px;
    border-right:1px solid black;
    box-sizing:border-box;
    margin-right:40px;
    font-size:14px;
}

.headerTypeSubMenuWrap ul:last-child {
    border:none;
}

.headerTypeSubMenuWrap ul li {
    margin-bottom:7px;
    width:fit-content;
}

.headerTypeSubMenuWrap ul li:hover {
    cursor:pointer;
    text-decoration:underline;
}
/* //Header css */

/* Section css */
    /* FirstSection css */
.firstSecWrap {
    height:500px;
    padding-top:70px;
}

.firstSecSubWrap {
    width:800px;
    height:300px;
    margin:0 auto;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    align-items:center;
    position:relative;
}

.firstSecSubWrap h4 {
    font-size:30px;
}

.sellers {
    display:flex;
    width:300vw;
    height:155px;
    transform:translateX(100vw);
}

.sellersWrap {
    display:flex;
    justify-content:space-around;
    flex:1;
}

.sellersSubWrap {
    display:flex;
    justify-content:space-between;
    width:700px;
}

.sellersSubWrap img {
    cursor:pointer;
}

.sellersBtnWrap {
    margin-top:10px;
    display:flex;
    justify-content:space-between;
    width:100px;
    font-size:20px;
}

.sellersBtn:hover {
    cursor:pointer;
}
    /* //FirstSection css */

    /* SecondSection css */
.secondSecWrap {
    height:500px;
}

.secondSecSubWrap {
    width:700px;
    margin:0 auto;
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-between;
}

.secondSecLeftWrap {
    width:350px;
    height:220px;
    margin-left:30px;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
}

.secondSecLeftWrap h4 {
    font-size:30px;
}

.secondSecLeftWrap h5 {
    font-size:18px;
    margin:0 0 3px 0;
}

.secondSecLeftWrap h4,
.secondSecLeftWrap h5 {
    color: #473932;
}

.secondSecLeftWrap p {
    font-size:14px;
}

.secondSecLeftWrap button {
    font-size:11px;
    width:90px;
    height:30px;
    display:flex;
    justify-content:center;
    align-items:center;
    background:rgba(250, 224, 212, 0.4);
    border:none;
}

.secondSecLeftWrap button:hover {
    cursor:pointer;
}
    /* //SecondSection css */

    /* ThirdSection css */
.thirdSecWrap {
    position:relative;
    height:700px;
}

.thirdSecSubWrap {
    width:800px;
    height:inherit;
    margin:0 auto;
}

.thirdSecSubWrap::after {
    content:'BESTBOOK';
    display:block;
    position:absolute;
    font-size:145px;
    top:-15px;
    color:rgba(255, 242, 230, 0.7);
    z-index:-1;
}

.thirdSecTopWrap {
    display:flex;
    flex-direction:column;
    align-items:center;
    color:#473932;
}

.thirdSecTopWrap h4 {
    font-size:30px;
}

.thirdSecTopWrap h5 {
    font-size:19px;
    font-weight:bold;
}

.thirdSecBtmWrap {
    display:flex;
    justify-content:space-between;
    margin-top:20px;
    height:400px;
}

.thirdSecImgLink:nth-of-type(odd) {
    align-self:flex-end;
}

.thirdSecImg {
    display:flex;
    width:190px;
    height:350px;
    background-position:center;
}

.thirdSecImg div {
    align-self:flex-end;
    display:flex;
    justify-content:center;
    align-items:center;
    width:inherit;
    height:100px;
    background:#FFFFFF4D;
}

.thirdSecImg div p {
    text-align:center;
    width:150px;
    font-size:16px;
    color:rgba(255, 242, 230);
    transition:0.5s;
}
    /* //ThirdSection css */

    /* FourthSection css */
.fourthSecWrap {
    height:500px;
}

.fourthSecSubWrap {
    display:flex;
    width:800px;
    height:400px;
    margin:0 auto;
}

.fourthSecSubWrap img {
    width:350px;
}

.fourthSecRightWrap {
    align-self:flex-end;
    width:100%;
    height:370px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    background:rgba(160, 134, 122, 0.1);
}

.fourthSecSubWrap h4 {
    margin-top:20px;
    font-size:32px;
}

.fourthSecSubWrap h5 {
    font-size:20px;
    margin:15px 0 20px 0;
}

.fourthSecSubWrap h4,
.fourthSecSubWrap h5 {
    color: #473932;
}

.fourthSecSubWrap ul {
    height:180px;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    align-items:center;
}

.fourthSecSubWrap li {
    font-size:13px;
    text-align:center;
}
    /* //FourthSection css */
/* //Section css */

/* Footer css */
.footer {
    position:relative;
    height:200px;
    background: rgb(60, 0, 0);
}

.footerWrap {
    width:800px;
    height:100%;
    display:flex;
    flex-direction:column;
    margin:0 auto;
}

.footerTopWrap {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    width:210px;
    height:135px;
    margin-left:10px;
}

.footerOrigin {
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    width:130px;
    height:50px;
    color: white;
    margin-top:15px;
}

.footerOrigin b {
    font-size:23px;
    line-height:23px;
}

.footerPlatform {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
}

.footerPlatform img {
    width:33px;
    height:33px;
}

.footerBtmWrap {
    flex-grow:1;
    display:flex;
    align-items:center;
}

.footerBtmWrap ul {
    display:flex;
    width:720px;
    justify-content:space-between;
    margin-left:30px;
}

.footerBtmWrap ul li {
    display:flex;
    align-items:center;
}

.footerBtmWrap li::after {
    content:"";
    display:block;
    width:1px;
    height:12px;
    background:gray;
    margin-left:8px;
}

.footerBtmWrap li:last-child::after {
    display:none;
}

.footerCompany {
    position:absolute;
    right:15px;
    bottom:2px;
    color:dimgray;
}

.footerCompany:hover {
    text-decoration:underline !important;
}
/* //Footer css */

/* Dynamic css */
@media (max-width: 820px) {
    .header {
        overflow:hidden;
    }

    .headerIconsWrap {
        right:20px;
    }

    .headerBtnWrap {
        justify-content:space-around;
    }

    .bannerImg {
        height:250px;
    }

    .secondSecWrap {
        height:700px;
    }

    .secondSecSubWrap {
        height:550px;
        flex-direction:column;
    }

    .thirdSecWrap {
        height:1000px;
    }

    .thirdSecTopWrap {
        text-align:center;
    }

    .thirdSecTopWrap {
        line-height:30px;
    }

    .thirdSecSubWrap::after {
        display:flex;
        justify-content:center;
        align-items:center;
        width:100vw;
        font-size:100px;
    }

    .thirdSecBtmWrap {
        width:500px;
        display:grid;
        grid-template-columns:repeat(2, 1fr);
        grid-row-gap:20px;
        place-items:center;
        margin:auto auto;
    }

    .thirdSecImg {
        display:flex;
        width:190px;
        height:350px;
        background-position:center;
    }

    .fourthSecWrap {
        height:700px;
    }

    .fourthSecSubWrap {
        width:100vw;
        height:600px;
        flex-direction:column;
        align-items:center;
    }

    .fourthSecSubWrap img {
        width:300px;
        height:300px;
    }

    .fourthSecRightWrap {
        align-self:center;
        justify-content:flex-start;
        width:500px;
    }

    .footer {
        height:300px;
    }

    .footerBtmWrap ul {
        display:grid;
    }

    .footerBtmWrap li::after {
        display:none;
    }

    .headerWrap,
    .headerBtnWrap,
    .firstSecSubWrap,
    .sellersWrap,
    .secondSecSubWrap,
    .thirdSecSubWrap {
        width:100vw;
    }
}
/* //Dynamic css */
